.review__image {
  object-fit: cover;
  width: 100%;
  height: 500px;
  max-width: 400px;
}

.article__wrapper {
  display: flex;
  flex-direction: column;
  padding: 1em;
  color: black;
  background-color: white;
}

.review__content {
  margin: 1em;
}

.review__tastenotes {
  width: "fit-content";
  padding: 15;
}

.navigate__back--link {
  width: "fit-content";
  padding-left: 15;
  color: white;
  position: "fixed";
  left: 20;
  top: 80;   
}

.navigate__back--arrow {
  padding-left: 15; 
  font-size: 20px;
  color: black
}

@media only screen and (min-width: 600px) {
  .review {
    display: flex;
    align-items: center;
  }
}