.horizontal__display {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
}

.horizontal__display img {
  object-fit: cover;
  height: 250px;
  width: 250px;
}

.vertical__display img {
  width: 100%;
  max-width: 150px;
}

.filter__button {
  padding: 16px;
  width: 25%;
  background-color: #634832;
  border: none;
  color: white;
  border-bottom: 1px solid black;
}

.filter__button:hover {
  cursor: pointer;
}

.review__teaser--headline {
  color: black;
  padding-left: 16px
}


.dropdown-menu {
  position: sticky;
  top: 0;
  padding: -1px;
}
